import React, { Component } from 'react'
import PropTypes from 'prop-types'
import './../../App.css'
import ReactChartkick, { PieChart } from 'react-chartkick'
import Chart from 'chart.js'

ReactChartkick.addAdapter(Chart)
export default class MacroNutrients extends Component {
  static propTypes = {
    macroNutrients: PropTypes.any.isRequired
  }

  static defaultPropTypes = {
    macroNutrients: {
      calories: 0,
      caloriesUnits: 'kcal',
      caloriesInUnits: 0,
      proteins: 0,
      proteinUnits: 'μg',
      proteinsInUnits: 0,
      carbohydrates: 0,
      carbohydratesUnits: 'μg',
      carbohydratesInUnits: 0,
      fat: 0,
      fatUnits: 'μg',
      fatInUnits: 0
    }
  }

  render() {
    const total = this.props.macroNutrients.proteins + this.props.macroNutrients.carbohydrates + this.props.macroNutrients.fat;
    return (
            <div className="macroNutrients">
                <div className="macroNutrient">
                    <div>Calories</div>
                    <div>{this.props.macroNutrients.caloriesInUnits} {this.props.macroNutrients.caloriesUnits}</div>
                </div>
                {
                  (this.props.macroNutrients.proteins ||
                  this.props.macroNutrients.carbohydrates || 
                  this.props.macroNutrients.fat
                  ) ? <div className="macroNutrient">
                  <PieChart  suffix="%" legend={false} height="100px" width="100px"  data={[
                    ["Proteins", parseInt(this.props.macroNutrients.proteins * 100 / total, 10) || 0], 
                    ["Carbs", parseInt(this.props.macroNutrients.carbohydrates * 100 / total, 10) || 0], 
                    ['Fat', parseInt(this.props.macroNutrients.fat * 100 / total, 10) || 0]
                    ]} />
                </div> : null
                }
                
                
                <div className="macroNutrient">
                    <div>Proteins</div>
                    <div>{this.props.macroNutrients.proteinsInUnits} {this.props.macroNutrients.proteinUnits}</div>
                </div>
                <div className="macroNutrient">
                    <div>Carbs</div>
                    <div>{this.props.macroNutrients.carbohydratesInUnits} {this.props.macroNutrients.carbohydratesUnits}</div>
                </div>
                <div className="macroNutrient">
                    <div>Fats</div>
                    <div>{this.props.macroNutrients.fatInUnits} {this.props.macroNutrients.fatUnits}</div>
                </div>
            </div>
    )
  }
}
